import styles from './RacasHorda.css'

import orc from './images/orc.png'
import undead from './images/undead.png'
import tauren from './images/tauren.png'
import troll from './images/troll.png'
import bloodelf from './images/bloodelf.png'

import RacasHordaimg from './RacasHordaimg'


export default function RacasHorda() {
    return(
        <div className={``}>
            <div className={`col QRaca  d-inline-block`}data-aos="zoom-out">
                <RacasHordaimg raca="Horda" title="Orc" img={orc}/>
                <p className={`quadro text-center `}>Orc</p>
            </div>
            <div className={`col QRaca  d-inline-block`}data-aos="zoom-out">
                <RacasHordaimg raca="Horda" title="Undead" img={undead}/>
                <p className={`quadro text-center`}>Undead</p>
            </div>
            <div className={`col QRaca  d-inline-block`}data-aos="zoom-out">
                <RacasHordaimg raca="Horda" title="Troll" img={troll}/>
                <p className={`quadro text-center`}>Troll</p>
            </div>
            <div className={`col QRaca  d-inline-block`}data-aos="zoom-out">
                <RacasHordaimg raca="Horda" title="Tauren" img={tauren}/>
                <p className={`quadro text-center`}>Tauren</p>
            </div>
            <div className={`col QRaca  d-inline-block`}data-aos="zoom-out">
                <RacasHordaimg raca="Horda" title="Blood Elf" img={bloodelf}/>
                <p className={`quadro text-center`}>Blood Elf</p>
            </div>
        </div>
    )
}