import styles from './BossName.css'


export default function BossName({imgone,bossnameone}) {
    return(
        <div className={`Boss `}>
            <div className='container BossBox row'>
                <div className={`BossMinBox`}>
                    <div className={`BossItemTop col-12 col-xxl-4 col-xl-4 col-lg-4 col-md-6 col-sm-6`}>
                        <h4 className={`text-center c-w `}>{bossnameone}</h4>
                    </div>
                    <div className={`BossItem col-12 col-xxl-4 col-xl-4 col-lg-4 col-md-6 col-sm-6`}>
                        <img className={`BossImg`} src={imgone} alt={imgone} />
                    </div>
                </div>
            </div>

        </div>
    )
}