import styles from './MountCard.css'


export default function MountCard({mountname, mountimg}) {
    return (
        <div className={` CardMount col col-xxl-2 col-xl-2 col-lg-3 col-md-4 col-sm-4 d-flex justify-content-center align-items-center`} data-aos="zoom-in">
            <div>
                <div>
                    <img className={`mount`} src={mountimg} alt="Worlf"   data-aos="zoom-out" data-aos-deley="2000"/>
                </div>
                <div className={`d-block`}>
                    <p className={`text-center `}  data-aos="zoom-out" data-aos-deley="2000">{mountname}</p>
                </div>
            </div>
        </div>
    )
}