import styles from './Mounts.css'
import MountCard from './MountCard/MountCard'
import Line from '../Line/Line'


import wolf from './MountCard/images/wolf.png'
import undeadhorse from './MountCard/images/undeadhorse.png'
import blacktiger from './MountCard/images/blacktiger.png'
import bluedrake from './MountCard/images/bluedrake.png'
import tapete from './MountCard/images/tapete.png'
import tartaruga from './MountCard/images/tartaruga.png'
import timelost from './MountCard/images/timelost.png'


export default function Mounts() {
    return(
        <div id="mounts">
            <div className={` row m-auto `}>
                <Line />
                <h1 className={`text-center mt-3 mb-5`} data-aos="zoom-in">Montarias</h1>
                <div className={` BoxCards `}>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={wolf} 
                        mountname="Wolf"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={undeadhorse} 
                        mountname="Undead Horse"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={blacktiger} 
                        mountname="Black Tiger"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={bluedrake} 
                        mountname="Blue Proto Drake"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={tapete} 
                        mountname="Tapete"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={tartaruga} 
                        mountname="Tartaruga"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={timelost} 
                        mountname="Time Lost Proto Drake"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={tartaruga} 
                        mountname="Tartaruga"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={timelost} 
                        mountname="Time Lost Proto Drake"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={bluedrake} 
                        mountname="Blue Proto Drake"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={undeadhorse} 
                        mountname="Undead Horse"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={tapete} 
                        mountname="Tapete"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={blacktiger} 
                        mountname="Black Tiger"/>
                    </div>
                    <div className={`d-inline-block child`}>
                        <MountCard 
                        mountimg={bluedrake} 
                        mountname="Blue Proto Drake"/>
                    </div>
                </div>
            </div>
        </div>
    )
}