import styles from './RacasAliance.css'
import RacasAlianceimg from './RacasAlianceimg'

import human from './images/human.png'
import nightelf from './images/nightelf.png'
import tauren from './images/tauren.png'
import troll from './images/troll.png'
import bloodelf from './images/bloodelf.png'


export default function RacasAlianca() {
    return(
        <div className={``}>
            <div className={`col QRaca d-inline-block`}>
                <RacasAlianceimg raca="Horda" title="human" img={human}/>
                <p className={`quadro text-center `}>Human</p>
            </div>
            <div className={`col QRaca d-inline-block`}>
                <RacasAlianceimg raca="Horda" title="nightelf" img={nightelf}/>
                <p className={`quadro text-center`}>nightelf</p>
            </div>
            <div className={`col QRaca d-inline-block`}>
                <RacasAlianceimg raca="Horda" title="Troll" img={troll}/>
                <p className={`quadro text-center`}>Troll</p>
            </div>
            <div className={`col QRaca d-inline-block`}>
                <RacasAlianceimg raca="Horda" title="Tauren" img={tauren}/>
                <p className={`quadro text-center`}>Tauren</p>
            </div>
            <div className={`col QRaca d-inline-block`}>
                <RacasAlianceimg raca="Horda" title="Blood Elf" img={bloodelf}/>
                <p className={`quadro text-center`}>Blood Elf</p>
            </div>
        </div>
    )
}