import styles from './Ls.css'

import lk from './images/lk.png'
import sindra from './images/sindra.png'
import blizz from './images/blizz.png'

export default function Lk() {
    return(
        <div className={`lkF d-flex justify-content-center align-items-center`}>
            <div className={`container`}>
                <img className={`Lk`} src={lk} alt="lk" />
                <img className={`Sindra`} src={sindra} alt="sindragosa" />
                <img className={`Blizzard`} src={blizz} alt="Blizzard" />
            </div>
        </div>
    )
}