import styles from './DoungeonItem.css'

export default function DoungeonItem({dgimg, title}){
    return(
        <div className={`DoungeonItem col-12 col-xxl-4 col-xl-4 col-lg-6 col-md-12 col-sm-12`} data-aos="zoom-out">
            <h4 className={`text-center`} data-aos="zoom-out">{title}</h4>
            <img src={dgimg} alt="Doungeon"/>
        </div>
    )
}