import React, { useState, useEffect } from 'react';
import './Instances.css';
import Line from '../Line/Line'

import BossName from './BossName/BossName';
import RaidInfNax from './RaidInf/RaidInfNax'
import RaidInfUlduar from './RaidInf/RaidInfUlduar'
import RaidInfToc from './RaidInf/RaidInfToc'
import RaidInfIcc from './RaidInf/RaidInfIcc'
import RaidInfRuby from './RaidInf/RaidInfRuby'

import raidnaxx from './images/raidnaxx.jpg'
import raidulduar from './images/raidulduar.jpg'
import raidtoc from './images/raidtoc.jpg'
import raidicc from './images/raidicc.jpg'
import raidruby from './images/raidruby.jpg'

export default function Instances() {
  const [activeTab, setActiveTab] = useState('');

  const handleTabClick = (event) => {
    event.preventDefault();
    const clickedTab = event.target.getAttribute('href').substring(1);
    setActiveTab(clickedTab);

    const tabs = document.querySelectorAll('.nav-link');
    tabs.forEach((tab) => {
      if (tab === event.target) {
        tab.classList.add('active');
      } else {
        tab.classList.remove('active');
      }
    });
  };

  useEffect(() => {
    const tabs = document.querySelectorAll('.nav-link');
    const firstTab = tabs[0];
    firstTab.classList.add('active');
    setActiveTab(firstTab.getAttribute('href').substring(1));
  }, []);

  return (
    <div id="InstanceS">
      <Line />
      <h1 className={`text-center mb-5`}>Instancias</h1>
      <div className="Instance container">
        <ul className="nav nav-tabs nav-justified">
          <li className={`nav-item ${activeTab === 'naxxramas' ? 'active' : ''}`}>
            <a href="#naxxramas" className="nav-link" onClick={handleTabClick}>
              naxxramas
            </a>
          </li>
          <li className={`nav-item ${activeTab === 'Ulduar' ? 'active' : ''}`}>
            <a href="#Ulduar" className="nav-link" onClick={handleTabClick}>
              Ulduar
            </a>
          </li>
          <li className={`nav-item ${activeTab === 'TOC' ? 'active' : ''}`}>
            <a href="#TOC" className="nav-link" onClick={handleTabClick}>
              T.O.C.
            </a>
          </li>
          <li className={`nav-item ${activeTab === 'Icecrown' ? 'active' : ''}`}>
            <a href="#Icecrown" className="nav-link" onClick={handleTabClick}>
              ICC
            </a>
          </li>
          <li className={`nav-item ${activeTab === 'RubySanctuary' ? 'active' : ''}`}>
            <a href="#RubySanctuary" className="nav-link" onClick={handleTabClick}>
              Ruby Sanctum
            </a>
          </li>
        </ul>
        <div className="tab-content">
          <div id="naxxramas" className={`tab-pane ${activeTab === 'naxxramas' ? 'active' : ''}`}>
            <div className="page-content FNaxx">
            <RaidInfNax 
              raidname="Naxxramas"
              raidimg={raidnaxx} />
            </div>
          </div>
          <div id="Ulduar" className={`tab-pane ${activeTab === 'Ulduar' ? 'active' : ''}`}>
            <div className="page-content FUlduar">
              <RaidInfUlduar 
              raidname="Ulduar"
              raidimg={raidulduar} />
            </div>
          </div>
          <div id="TOC" className={`tab-pane ${activeTab === 'TOC' ? 'active' : ''}`}>
            <div className="page-content Ftoc">
            <RaidInfToc 
              raidname="Trial Of Crusader"
              raidimg={raidtoc} />
            </div>
          </div>
          <div id="Icecrown" className={`tab-pane ${activeTab === 'Icecrown' ? 'active' : ''}`}>
            <div className="page-content FIce">
            <RaidInfIcc 
              raidname="Ice Crown Cidadel"
              raidimg={raidicc} />
            </div>
         
            </div>
          <div id="RubySanctuary" className={`tab-pane ${activeTab === 'RubySanctuary' ? 'active' : ''}`}>
            <div className="page-content FRuby">
            <RaidInfRuby 
              raidname="The Ruby Sanctun"
              raidimg={raidruby} />
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}