import styles from './BattleGround.css'
import Line from '../Line/Line'
import Bgs from './Bgs/Bgs'

import eye from './images/bgeye.jpg'
import isle from './images/bgisle.jpg'
import warsong from './images/bgwarsong.jpg'
import arathi from './images/bgarathi.jpg'
import alterac from './images/bgalterac.jpg'
import dalaranarena from './images/dalaranarena.jpg'
import ringoftrial from './images/ringsoftrialarena.jpg'
import circleofblood from './images/circleofbloodarena.jpg'

export default function BattleGround() {
    return(
        <div id="BattleGround" className={`pb-5`}>
            <Line />
            <h1 className={`text-center pb-5`}>Campos de Batalha</h1>
            <div className={`BattleGroundBox container row m-auto pt-4 ps-0 pe-0`}>
                <div className={`Children col-sm-12 col-md-12 col-lg-6 col-xl-6 col-xxl-6`}>
                    <h3 className={`text-center mb-4`}>Campo de batalha</h3>
                    <p>Espao para os amadores que procuram leves emoes e dispustas injustas.</p>
                    <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry.  Lorem Ipsum is simply dummy text of the printing and typesetting industry. </p>
                    <Bgs
                    imgbg={eye}
                    infbgs="15/15 s massacre" 
                    titlebg="Eye Of The Storn"/>
                    <Bgs
                    imgbg={isle}
                    infbgs="15/15 s massacre"
                    titlebg="Isle of Conquest" />
                    <Bgs
                    imgbg={warsong}
                    infbgs="10/10 s massacre"
                    titlebg="Warsong Gulch" />
                    <Bgs
                    imgbg={arathi}
                    infbgs="15/15 s massacre"
                    titlebg="Arathi Basin" />
                    <Bgs
                    imgbg={alterac}
                    infbgs="40/40 Mais massacre ainda.."
                    titlebg="Alterac Valey" />


                </div>
                <div className={`Ranked col-sm-12 col-md-12 col-lg-6 col-xl-6 col-xxl-6`}>
                    <h3 className={`text-center mb-4`}>Ranqueada</h3>
                    <p>Aqui  onde o filho chora e a me no ve, no tem peito nem entra filho. Emocionados sero esmagados sem nemhuma piedade.</p>
                    <p>Lorem Ipsum is simply dummy text of the printing and typesetting industry. </p>
                    <Bgs
                    imgbg={circleofblood}
                    infbgs="2/2 - 3/3 s os brabo" 
                    titlebg="Circle of Blood"/>
                    <Bgs
                    imgbg={ringoftrial}
                    infbgs="2/2 - 3/3 s os brabo" 
                    titlebg="Rings of Trial"/>
                    <Bgs
                    imgbg={dalaranarena}
                    infbgs="2/2 - 3/3 s os brabo" 
                    titlebg="Dalaran Arena"/>


                </div>
            </div>
        </div>
    )
}