import styles from './Doungeons.css'
import Line from '../Line/Line'
import DoungeonItem from './DoungeonsItem/DoungeonItem'

import dg1 from './images/dg1.jpg'
import dg2 from './images/dg2.jpg'
import dg3 from './images/dg3.jpg'
import dg4 from './images/dg4.jpg'
import dg5 from './images/dg5.jpg'
import dg6 from './images/dg6.jpg'

export default function Doungeons() {
    return(
        <div id="Doungeons">
            <div>
                <Line />
                <h1 className={`text-center m-0`} data-aos="zoom-out">Masmorras</h1>
                <div className={`DoungeonBox container row m-auto pt-5 pb-5`}>
                    <DoungeonItem 
                    title="Titulo 1"
                    dgimg={dg1}/>
                    <DoungeonItem 
                    title="Titulo 2"
                    dgimg={dg2}/>
                    <DoungeonItem 
                    title="Titulo 3"
                    dgimg={dg3}/>
                    <DoungeonItem 
                    title="Titulo 4"
                    dgimg={dg4}/>
                    <DoungeonItem 
                    title="Titulo 5"
                    dgimg={dg5}/>
                    <DoungeonItem 
                    title="Titulo 1"
                    dgimg={dg1}/>
                    <DoungeonItem 
                    title="Titulo 2"
                    dgimg={dg2}/>
                    <DoungeonItem 
                    title="Titulo 6"
                    dgimg={dg6}/>
                    <DoungeonItem 
                    title="Titulo 3"
                    dgimg={dg3}/>
                    <DoungeonItem 
                    title="Titulo 4"
                    dgimg={dg4}/>
                    <DoungeonItem 
                    title="Titulo 4"
                    dgimg={dg4}/>
                    <DoungeonItem 
                    title="Titulo 5"
                    dgimg={dg5}/>
                </div>
            </div>
        </div>
    )
}