import { useState, useEffect, useRef } from 'react';

import c1 from './images/c1.jpg';
import c2 from './images/c2.jpg';
import c3 from './images/c3.jpg';
import c4 from './images/c4.jpg';
import c5 from './images/c5.jpg';
import dkhome from './images/dkhome.png'
import worldofwarcraft from './images/worldofwarcraft.png'

import './Home.css';


export default function Home() {
  const [index, setIndex] = useState(0);
  const timerRef = useRef(null);

  const handleIndicatorClick = (selectedIndex) => {
    setIndex(selectedIndex);
    resetTimer();
  };

  const handleNextSlide = (direction) => {
    setIndex((prevIndex) => {
      if (direction === 'prev') {
        return (prevIndex - 1 + 5) % 5;
      } else if (direction === 'next') {
        return (prevIndex + 1) % 5;
      }
    });
    resetTimer();
  };

  const resetTimer = () => {
    clearInterval(timerRef.current);
    timerRef.current = setInterval(() => handleNextSlide('next'), 7000);
  };

  useEffect(() => {
    resetTimer();

    return () => {
      clearInterval(timerRef.current);
    };
  }, []);

  const handlePrevSlide = () => {
    handleNextSlide('prev');
  };

  return (
    <div id="home">
      <div className={`d-flex justify-content-center `}>
        <img className={`imgwow`} src={worldofwarcraft} alt="World Of Warcraft" />
      </div>
      <div id="banner" className="w-100 carousel slide" data-bs-ride="carousel">
        <ol className="carousel-indicators">
          <li
            data-bs-target="#banner"
            data-bs-slide-to="0"
            className={index === 0 ? 'active' : ''}
            onClick={() => handleIndicatorClick(0)}
          ></li>
          <li
            data-bs-target="#banner"
            data-bs-slide-to="1"
            className={index === 1 ? 'active' : ''}
            onClick={() => handleIndicatorClick(1)}
          ></li>
          <li
            data-bs-target="#banner"
            data-bs-slide-to="2"
            className={index === 2 ? 'active' : ''}
            onClick={() => handleIndicatorClick(2)}
          ></li>
          <li
            data-bs-target="#banner"
            data-bs-slide-to="3"
            className={index === 3 ? 'active' : ''}
            onClick={() => handleIndicatorClick(3)}
          ></li>
          <li
            data-bs-target="#banner"
            data-bs-slide-to="4"
            className={index === 4 ? 'active' : ''}
            onClick={() => handleIndicatorClick(4)}
          ></li>
        </ol>
        <div className="carousel-inner">
          <div className={`carousel-item ${index === 0 ? 'active' : ''} `}>
            <img className="imgcarolsel d-block w-100" src={c1} alt="Slide 1" />
          </div>
          <div className={`carousel-item ${index === 1 ? 'active' : ''} `}>
            <img className="imgcarolsel d-block w-100" src={c2} alt="Slide 2" />
          </div>
          <div className={`carousel-item ${index === 2 ? 'active' : ''} `}>
            <img className=" imgcarolsel d-block w-100" src={c3} alt="Slide 3" />
          </div>
          <div className={`carousel-item ${index === 3 ? 'active' : ''} `}>
            <img className=" imgcarolsel d-block w-100" src={c4} alt="Slide 4" />
          </div>
          <div className={`carousel-item ${index === 4 ? 'active' : ''} `}>
            <img className=" imgcarolsel d-block w-100" src={c5} alt="Slide 5" />
          </div>
          <div className={`boxinf d-flex justify-content-center align-items-center`}>
            <div className={`inf`}>
              <img className={`dkhome`} src={dkhome} alt="Death Knight" />
              <h5 className={`c-white mb-4`}>Death Knight</h5>
              <p className={`c-white`}>Venha Conhecer</p>
              <p className={`c-white`}>A ascenso do cavaleiro das trevas</p>
              <p className={`c-white`}>Lorem Ipsum is simply dummy text of the printing and typesetting industry. </p>
              <p className={`text-center buttondk `}><a href="#" target='_blank'>Clique e saiba mais</a></p>
            </div>
          </div>
        </div>
        <button
        className="carousel-control-prev carouselprev"
        type="button"
        data-bs-target="#banner"
        data-bs-slide="prev"
        onClick={handlePrevSlide}
      >
        <span className="carousel-control-prev-icon" aria-hidden="true"></span>
        <span className="visually-hidden">Previous</span>
      </button>
      <button
        className="carousel-control-next carouselnext"
        type="button"
        data-bs-target="#banner"
        data-bs-slide="next"
        onClick={() => handleNextSlide('next')}
      >
        <span className="carousel-control-next-icon" aria-hidden="true"></span>
        <span className="visually-hidden">Next</span>
      </button>
      </div>
    </div>
  );
}