import styles from './Bgs.css'

export default function Bgs({infbgs, imgbg, titlebg}) {
    return(
        <div id="Bgs">
                <h5 className={`text-center TitleBg`}>{titlebg}</h5>
                <img src={imgbg} alt="Battle Ground" />
                <p className={`text-center InfBg`}>{infbgs}</p>
        </div>
    )
}