import styles from './Divisor.css'

import alianceescudo from './images/alianceescudo.png'
import hordaescudo from './images/hordaescudo.png'
import lkira from './images/lkira.png'

export default function Divisor() {
    return(
        <div className={`Divisor`}>
            <div className={`w-100 d-flex justify-content-center flex-wrap`}>
                    <img className={` img-fluid EOpacity`} src={hordaescudo} alt="Horda Escudo" />
                    <img className={` img-fluid EOpacity`} src={alianceescudo} alt="Alianca Escudo" />
            </div>
        </div>
    )
}