import React, { useState } from 'react';
import styles from './Menu.css';
import wow from './wow.png';

export default function Menu() {
  const [isOpen, setIsOpen] = useState(false);

  const handleItemClick = () => {
    setIsOpen(false);
  };

  const toggleMenu = () => {
    setIsOpen(!isOpen);
  };

  const scrollToSection = (sectionId) => {
    const section = document.querySelector(sectionId);
    if (section) {
      const targetY = section.getBoundingClientRect().top + window.pageYOffset;
      const initialY = window.pageYOffset;
      const distance = targetY - initialY;
      const duration = 1000; 
      let start;

      window.requestAnimationFrame(function step(timestamp) {
        if (!start) start = timestamp;
        const elapsed = timestamp - start;
        const progress = Math.min(elapsed / duration, 1);
        window.scrollTo(0, initialY + distance * progress);

        if (elapsed < duration) {
          window.requestAnimationFrame(step);
        }
      });
    }
    handleItemClick();
  };

  return (
    <nav className={`nav navbar navbar-expand-sm fixed-top w-100`}>

      <div className={`container-fluid`}>
        <a className={`logowow`} href="index.html">
          <img className={`logowowwidth`} src={wow} alt="wow" />
        </a>
        <button
          className={`btnnav navbar-toggler ${isOpen ? 'collapsed' : ''}`}
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarNav"
          aria-controls="navbarNav"
          aria-expanded={isOpen ? 'true' : 'false'}
          aria-label="Toggle navigation"
          onClick={toggleMenu}
        >
          <span className={`navicon navbar-toggler-icon`}></span>
        </button>

        <div className={`collapse navbar-collapse justify-content-end ${isOpen ? 'show' : ''}`} id="navbarNav">
          <ul className={`navbar-nav`}>
            <li className={`nav-item `}>
              <a className={`aMenu`} href="#home" onClick={() => scrollToSection('#home')}>
                Incio
              </a>
            </li>
            <li className={`nav-item`}>
              <a className={`aMenu`} href="#faccao" onClick={() => scrollToSection('#faccao')}>
                Faces
              </a>
            </li>
            <li className={`nav-item`}>
              <a className={`aMenu`} href="#horda" onClick={() => scrollToSection('#horda')}>
                Horda
              </a>
            </li>
            <li className={`nav-item`}>
              <a className={`aMenu`} href="#aliance" onClick={() => scrollToSection('#aliance')}>
                Aliana
              </a>
            </li>
            <li className={`nav-item`}>
              <a className={`aMenu`} href="#" onClick={() => scrollToSection('#sobre')}>
                Sobre
              </a>
            </li>
          </ul>
        </div>
      </div>
    </nav>
  );
}
